<template>
  <div class="container">
    <div class="data" style="width: 100%">
      <common-table :columns="adviseColumn" :dataSource="adviseData"></common-table>
    </div>
  </div>
</template>

<script>
const CommonTable = () => import('@/components/CommonTable')
export default {
  name: 'LifeTableAdviseList',
  components: {CommonTable},
  data () {
    return {
      adviseColumn: [
        {prop: 'approval', width: 120, label: '当前审核人', align: 'center'},
        {prop: 'nextApproval', width: 120, label: '下级审核人', align: 'center'},
        {prop: 'approveTime', width: 120, label: '审核时间', align: 'center'},
        {prop: 'approveAdvise', width: 120, label: '审核意见', align: 'center'}
      ],
      adviseData: [
        {approval: '孙强', nextApproval: '张三', approveTime: '2018-05-05', approveAdvise: '同意'},
        {approval: '孙强', nextApproval: '张三', approveTime: '2018-04-04', approveAdvise: '继续补充'},
        {approval: '孙强', nextApproval: '张三', approveTime: '2018-03-03', approveAdvise: '继续修改'},
        {approval: '孙强', nextApproval: '张三', approveTime: '2018-02-02', approveAdvise: '建议补充'},
        {approval: '孙强', nextApproval: '张三', approveTime: '2018-01-01', approveAdvise: '建议修改'}
      ]
    }
  }
}
</script>

<style scoped>

</style>



